import React from "react"
import { Link } from "gatsby"
import styled from 'styled-components'

import SiteContext from '../context/SiteContext'
import IconButton from './styled/icon-button.js';

//#region Styled Components
const NavDialog = styled.div`
  pointer-events: none;
  width: 90vw;
  height: 90vh;

  position: fixed;
  top: 5vh;
  left: 5vw;
  z-index: -1;
  overflow: auto;

  @media only screen  and (min-width : 768px) {
    width: 80vw;
    height: 80vh;
    top: 10vh;
    left: 10vw;
    overflow: hidden;
  }

  padding: 2rem;
  box-sizing: border-box;

  font-weight: 100;
  color: #fff;
  background-color: var(--primary);

  h1, h4 {
    margin-bottom: .250rem;
  }

  nav {
    display: flex;
    flex-direction: column;
  }

  a {
    color: inherit;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
  }

  button${IconButton} {
    position: absolute;
    top: 0;
    right: 0;
  }

  &.is-grid {
    max-width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: auto;
  }

  .fp1, .fp2, .fp3, .fp4 {
    grid-column-start: 1;
    grid-column-end: span 4;
  }

  .fp1 {
    @media only screen  and (min-width : 768px) {
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 1;
      grid-row-end: 2;
    }
  }

  .fp2 {
    @media only screen  and (min-width : 768px) {
      grid-column-start: 1;
      grid-column-end: 1;
      grid-row-start: 2;
      grid-row-end: 2;
    }
  }

  .fp3 {
    @media only screen  and (min-width : 768px) {
      grid-column-start: 2;
      grid-column-end: 2;
      grid-row-start: 2;
      grid-row-end: 2;
    }
  }

  .fp4 {
    @media only screen  and (min-width : 768px) {
      grid-column-start: 3;
      grid-column-end: 5;
      grid-row-start: 1;
      grid-row-end: 3;
    }
  }
`

const NavButtonContainer = styled.div`
  width: 32px;
  height: 32px;

  position: fixed;
  top: 24px;
  right: 24px;
  bottom: auto;
  left: auto;
  z-index: 50;

  @media only screen  and (min-width : 768px) {
    top: auto;
    right: auto;
    bottom: 32px;
    left: calc(50% -  24px);
  }
`

const NavOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;

  width: 100vw;
  height: 100vh;

  opacity: 0;
  background: rgba(255,255,255,.8);
`

const NavContainer = styled.div`

  /* Add animation to nav elements */
  div${NavDialog},
  div${NavOverlay} {
    transition: all .2s;
  }

  /* (start) properties to animate */
  div${NavDialog} {
    transform: scale(0.98);
    opacity: 0;
  }

  div${NavDialog} {
    transform: scale(0.98);
    opacity: 0;
  }

  /* Open state */
  &.is-open div${NavDialog} {
    z-index: 100 !important;
    pointer-events: auto !important;
    transform: scale(1);
    opacity: 1;
  }

  &.is-open div${NavOverlay} {
    z-index: 90 !important;
    opacity: 1;
  }

  &.is-open div${NavButtonContainer} {
    display: none;
  }

  /* Other */
  button${IconButton} {
    background-color: var(--primary);
  }
`
//#endregion

// class Navigation extends React.Component {
function Navigation() {

  // Does not work? why
  // const [opened, setOpened] = useState(true);

  // This works better
  // const headingElm = React.createRef();

  // Works!
  // let myButton = null;
  // function changeColor() {
  //   headingElm.current.style.color = 'red';
  // }

  return (
    <SiteContext.Consumer>
      {SiteContext => (
        <NavContainer className={SiteContext.open ? 'is-open' : ''}>

          {/* <h1 ref={headingElm}>Some heading</h1>
          <button onClick={() => changeColor()}>
            Change color
          </button> */}

          {/* NavButtonContainer */}
          <NavButtonContainer>
            <IconButton
              onClick={SiteContext.toggleOpen.bind(this)}
              className="close-button">☰</IconButton>
          </NavButtonContainer>

          {/* NavDialog */}
          <NavDialog className="is-grid">
            {/* Close button */}
            <IconButton
              onClick={SiteContext.toggleOpen.bind(this)}
              className="close-button">✕</IconButton>

            {/* Panels */}
            <div className="title-panel fp1">
              <div>
                <strong className="title-accent">me</strong>
                <h1>Mick Schouten</h1>
                <h4>creative front-end developer</h4>
              </div>
            </div>
            <div className="title-panel fp2">
              <nav>
                <strong className="title-accent">navigate</strong>
                <Link onClick={SiteContext.toggleOpen.bind(this)} to="/">home</Link>
                <Link className="u-pointer-events--n u-text-decoration--lt" to="/about-me">about me</Link>
                <Link className="u-pointer-events--n u-text-decoration--lt" to="/work">work</Link>
                <Link className="u-pointer-events--n u-text-decoration--lt" to="/blog">blog</Link>
                <Link className="u-pointer-events--n u-text-decoration--lt" to="/contact">contact</Link>
              </nav>
            </div>
            <div className="title-panel fp3">
              <div>
                <strong className="title-accent">find me</strong>
                <a href="https://nl.linkedin.com/in/bravebox" target="_blank" rel="noopener noreferrer">Linkedin</a>
              </div>
            </div>
            <div className="title-panel fp4">
              <div>
                <strong className="title-accent">about me: TL;DR</strong>
                <p>
                  Hi folks. I am a 39 year old creative front-end dev from the Netherlands. I design and style UI/UX components, websites and applications.
                </p>
                <p>This site is still under construction.</p>
                <p><i><small>This website was made with <a href="https://www.gatsbyjs.org/" target="_blank" rel="noopener noreferrer">Gatsby</a></small></i></p>
              </div>
            </div>
          </NavDialog>

          {/* NavOverlay */}
          <NavOverlay></NavOverlay>
        </NavContainer>
      )}
    </SiteContext.Consumer>
  )
}

export default Navigation
